document.addEventListener("DOMContentLoaded", (event) => {
    const filterBox = document.querySelector('.catalog-sort');
    if (filterBox) {
        const filterBoxTop = filterBox.querySelector('.catalog-sort__current');
        const filterBoxList = filterBox.querySelector('.catalog-sort__body');
        const filterBoxItems = filterBox.querySelectorAll('.catalog-sort__item-to-choose');
        const clickOutBoxHandler = ({target}) => {
            if (!target.classList.contains('catalog-sort__current') && !target.closest('.catalog-sort__current')) {
                filterBoxList.removeAttribute('data-active');
            }
        };

        filterBoxItems.forEach(filterBoxItem => {
            filterBoxItem.addEventListener('click', (evt) => {
                if (!filterBoxItem.hasAttribute('data-active')) {
                    const activeCur = filterBoxList.querySelector('[data-active]');
                    activeCur?.removeAttribute('data-active');
                    filterBoxItem.setAttribute('data-active', true);
                    filterBoxTop.textContent = filterBoxItem.textContent;
                }
            });
        })

        filterBoxTop.addEventListener('click', (evt) => {
            if (filterBoxList.getAttribute('data-active')) {
                filterBoxList.removeAttribute('data-active');
            } else {
                filterBoxList.setAttribute('data-active', true);
                document.addEventListener('click', clickOutBoxHandler);
            }
        });
    }


    const viewBox = document.querySelector('.catalog-view');
    if (viewBox) {
        const catalog = viewBox.closest('.catalog__wrapper');
        const viewBoxItems = viewBox.querySelectorAll('.catalog-view__way');

        viewBoxItems.forEach(viewBoxItem => {
            viewBoxItem.addEventListener('click', (evt) => {
                if (!viewBoxItem.hasAttribute('data-active')) {
                    const activeCur = viewBox.querySelector('[data-active]');
                    if (activeCur) {
                        const catalogClass = activeCur.getAttribute('data-class');
                        activeCur?.removeAttribute('data-active');
                        if(catalogClass){
                            catalog.classList.remove(`${catalogClass}`);
                        }
                    }

                    const newCatalogClass = viewBoxItem.getAttribute('data-class');
                    if(newCatalogClass){
                        catalog.classList.add(`${newCatalogClass}`);
                    }
                    viewBoxItem.setAttribute('data-active', true);
                }
            });
        })
    }


    const catalogMoreBtns = document.querySelectorAll('.catalog-filter__more');
    if (catalogMoreBtns.length) {
        catalogMoreBtns.forEach(catalogMoreBtn => {
            const itemsBlock = catalogMoreBtn.closest('.catalog-filter__items');
            catalogMoreBtn.addEventListener('click', () => {
                const collapsedStatus = itemsBlock.getAttribute('data-collapsed');
                if (collapsedStatus === 'true') {
                    itemsBlock.setAttribute('data-collapsed', false);
                    catalogMoreBtn.textContent = catalogMoreBtn.getAttribute('data-hide');
                } else {
                    itemsBlock.setAttribute('data-collapsed', true);
                    catalogMoreBtn.textContent = catalogMoreBtn.getAttribute('data-show');
                }
            })
        });
    }
});

