const orderSettingBoxes = document.querySelectorAll('.order-settings-box');
if (orderSettingBoxes.length) {
    orderSettingBoxes.forEach(orderSettingBox => {
        const orderSettingBoxTop = orderSettingBox.querySelector('.order-settings-box__icon');
        const orderSettingBoxList = orderSettingBox.querySelector('.order-settings-box__list');
        const orderSettingBoxItems = orderSettingBox.querySelectorAll('.order-settings-box__list a');
        const clickOutBoxHandler = ({target}) => {
            if (!target.classList.contains('order-settings-box__icon') && !target.closest('.order-settings-box__icon')) {
                orderSettingBoxList.removeAttribute('data-active');
            }
        };

        orderSettingBoxItems.forEach(orderSettingBoxItem => {
            orderSettingBoxItem.addEventListener('click', (evt) => {
                if (!orderSettingBoxItem.hasAttribute('data-active')) {
                    const activeCur = orderSettingBoxList.querySelector('[data-active]');
                    activeCur?.removeAttribute('data-active');
                    orderSettingBoxItem.setAttribute('data-active', true);
                }
            });
        })

        orderSettingBoxTop.addEventListener('click', (evt) => {
            if (orderSettingBoxList.getAttribute('data-active')) {
                orderSettingBoxList.removeAttribute('data-active');
            } else {
                orderSettingBoxList.setAttribute('data-active', true);
                document.addEventListener('click', clickOutBoxHandler);
            }
        });
    });

}